/* Material-design-like card support. */

.z-depth-0 {
  -webkit-box-shadow: none !important;
          box-shadow: none !important;
}

.z-depth-1 {
  -webkit-box-shadow: 0 2px 2px 0 rgba(6, 55, 78, 0.14), 0 1px 5px 0 rgba(6, 55, 78, 0.12), 0 3px 1px -2px rgba(6, 55, 78, 0.2);
          box-shadow: 0 2px 2px 0 rgba(6, 55, 78, 0.14), 0 1px 5px 0 rgba(6, 55, 78, 0.12), 0 3px 1px -2px rgba(6, 55, 78, 0.2);
}

.z-depth-2 {
  -webkit-box-shadow: 0 4px 5px 0 rgba(6, 55, 78, 0.14), 0 1px 10px 0 rgba(6, 55, 78, 0.12), 0 2px 4px -1px rgba(6, 55, 78, 0.3);
          box-shadow: 0 4px 5px 0 rgba(6, 55, 78, 0.14), 0 1px 10px 0 rgba(6, 55, 78, 0.12), 0 2px 4px -1px rgba(6, 55, 78, 0.3);
}

.z-depth-3 {
  -webkit-box-shadow: 0 6px 10px 0 rgba(6, 55, 78, 0.14), 0 1px 18px 0 rgba(6, 55, 78, 0.12), 0 3px 5px -1px rgba(6, 55, 78, 0.3);
          box-shadow: 0 6px 10px 0 rgba(6, 55, 78, 0.14), 0 1px 18px 0 rgba(6, 55, 78, 0.12), 0 3px 5px -1px rgba(6, 55, 78, 0.3);
}

.z-depth-4 {
  -webkit-box-shadow: 0 8px 10px 1px rgba(6, 55, 78, 0.14), 0 3px 14px 2px rgba(6, 55, 78, 0.12), 0 5px 5px -3px rgba(6, 55, 78, 0.3);
          box-shadow: 0 8px 10px 1px rgba(6, 55, 78, 0.14), 0 3px 14px 2px rgba(6, 55, 78, 0.12), 0 5px 5px -3px rgba(6, 55, 78, 0.3);
}

.z-depth-5 {
  -webkit-box-shadow: 0 16px 24px 2px rgba(6, 55, 78, 0.14), 0 6px 30px 5px rgba(6, 55, 78, 0.12), 0 8px 10px -5px rgba(6, 55, 78, 0.3);
          box-shadow: 0 16px 24px 2px rgba(6, 55, 78, 0.14), 0 6px 30px 5px rgba(6, 55, 78, 0.12), 0 8px 10px -5px rgba(6, 55, 78, 0.3);
}

button.btn-link:active {
  /* fix border around btn-link elements */
  background-color: transparent !important;
  border-color: transparent !important;
}

.input-group input,
.input-group button {
  /* fix weird pixel misalignment on input groups */
  height: 40px;
}

h1 {
  /* fix h1 top margin */
  margin-top: 0rem;
}